import config from "./structure.json";
import { ADD_INPUT_BINDING_VALUE } from "../../reducer/action/inputForm";

const inputSelectBindingFunc = (thisElement) => {
  thisElement.patterns[1].disabled =
    thisElement.values[0].slice(-3) === "<T>" ? false : true;
};

export const inputSelectBindingTemplate = (() => {
  return {
    type: "input_select_binding",
    patterns: [
      {
        type: "Select",
        width: 30,
        swidth: 30,
        options: config.options,
      },
      {
        type: "Input",
        width: 30,
        swidth: 30,
        disabled: true,
        placeholder: "For replace <T>",
      },
      {
        type: "Input",
        width: 30,
        swidth: 30,
        placeholder: "Variable Name",
      },
    ],
    middlewareFunc: inputSelectBindingFunc,
    values: ["int", "", ""],
  };
})();

const dataStruct = (() => {
  return {
    elements: [
      {
        type: "title",
        title: "Decide Structure",
      },
      {
        type: "input_select_binding",
        patterns: [
          {
            type: "Select",
            width: 30,
            swidth: 30,
            options: config.options,
          },
          {
            type: "Input",
            width: 30,
            swidth: 30,
            disabled: true,
            placeholder: "For replace <T>",
          },
          {
            type: "Input",
            width: 30,
            swidth: 30,
            placeholder: "Variable Name",
          },
        ],
        middlewareFunc: inputSelectBindingFunc,
        values: ["int", "", ""],
      },
    ],
    footers: [{ type: "add_button", actionHandleClick: ADD_INPUT_BINDING_VALUE }],
  };
})();

export default dataStruct;
