import styled from "styled-components";

export const Order = styled.h1`
  position: relative;
  top: 90%;
  right: 0;
`;

export const WindowLayout = styled.div`
  background-color: transparency;
  display: flex;
  width: 200px;
  border-radius: 10px;
  padding: 5px;
  font-size: 5px;
  border: ${(props) => (props.isThisPage ? "4px solid red" : "")};
  flex-shrink: 0;
  &:hover {
    border: 4px solid blue;
  }
`;

export const FloatingWindowLayout = styled.div`
  overflow-x: scroll;
  overflow-y: hidden;
  display: flex;
  flex-wrap: nowrap;
  height: 100%;
  &:hover {
    cursor: pointer;
  }
}
`;
