import styled from "styled-components";

export const AlignLayout = styled.div`
  text-align: ${(props) => (props.align ? props.align : "center")};
  display: flex;
  flex-grow: 0;
  width: 100%;
  justify-content: center;
`;

export const FixedLayout = styled.div`
  position: fixed;
  top: ${(props) => props.top};
  left: ${(props) => props.left};
  right: ${(props) => props.right};
`;

export const BodyLayout = styled.div`
  overflow-y: scroll;
  margin: auto;
  width: 50%;
  height: 68vh;
  &::-webkit-scrollbar {
    width: 0;
  }
`;

export const HeadLayout = styled.div`
  height: 28vh;
  margin-bottom: 2vh;
`;

export const APILayout = styled.div`
  overflow: hidden;
`;
