import {
  SET_STRUTURE,
  SET_COST_VARIABLE_INDEX,
} from "../../action/storage";
import dataStructure from "./initStroge";

const storage = (state = dataStructure, action) => {
  switch (action.type) {
    case SET_STRUTURE: {
      const nextState = { ...state };

      nextState.dataStructure = action.payload;

      return nextState;
    }
    case SET_COST_VARIABLE_INDEX: {
      const nextState = { ...state };

      nextState.costVariableIndex = action.payload;

      return nextState;
    }
    
    default: {
      return state;
    }
  }
};

export default storage;
