import map from "lodash/map";
import React, { useCallback, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import TitleBar from "../../atoms/TitleBar";
import {
  SET_COST_VARIABLE_INDEX,
  SET_STRUTURE,
} from "../../reducer/action/storage";
import { AlignLayout } from "../../styles/globalStyle";
import { AlertBar, Option, OptionLayout } from "./style";

function Compator(props) {
  const structureData = useSelector(
    (state) => state.structure.pages[0].elements
  );
  const { costVariableIndex, dataStructure } = useSelector(
    (state) => state.storage
  );
  const dispatch = useDispatch();

  useEffect(() => {
    let tString = "";
    map(structureData, (input) => {
      if (!input.values) return;

      if (input.values[0].slice(-3) === "<T>") {
        if (!input.values[0] || !input.values[1] || !input.values[2]) return;
        tString += `${input.values[0].slice(0, -3)} <${input.values[1]}> ${
          input.values[2]
        }`;
      } else {
        if (!input.values[0] || !input.values[2]) return;
        tString += `${input.values[0]} ${input.values[2]}`;
      }
      tString += "\n";
    });

    dispatch({ type: SET_STRUTURE, payload: tString });
  }, [structureData, dispatch]);

  const handleClick = useCallback(
    (index) => {
      return () => dispatch({ type: SET_COST_VARIABLE_INDEX, payload: index });
    },
    [dispatch]
  );

  return (
    <>
      <TitleBar title={"Decide cost variable"} {...props} />
      <AlignLayout>
        <OptionLayout>
          {map(dataStructure.split("\n").slice(0, -1), (option, index) => {
            return (
              <Option
                key={index}
                onClick={handleClick(index)}
                selected={costVariableIndex === index}
                {...props}
                disabled={option.indexOf("<") !== -1}
              >
                {option}
              </Option>
            );
          })}
        </OptionLayout>
      </AlignLayout>
      {costVariableIndex === -1 && (
        <AlertBar title={"* Didn't decide cost variable"} />
      )}
    </>
  );
}

const compator = (() => {
  return {
    customComponent: Compator,
  };
})();

export default compator;
