import styled from "styled-components";

export const KeyboardArrowRightIcon = styled.button`
  display: ${(props) => (props.disabled ? "none" : "flex")};
  color: gray;
  background-color: pink;
  border-radius: 25px;
  margin: 10px;
  border: none;
  &:hover {
    cursor: pointer;
    color: #b8860b;
  }
`;
