import React, { forwardRef } from "react";
import map from "lodash/map";
import { FloatingWindowLayout } from "./style";
import CombineWindow from "./CombineWindow";

const FloatingWindow = forwardRef((props, ref) => {
  const { step, pages } = props;

  return (
    <FloatingWindowLayout ref={ref}>
      {map(pages, (page, index) => {
        return (
          <CombineWindow key={index} index={index} step={step} {...props} />
        );
      })}
    </FloatingWindowLayout>
  );
});

export default FloatingWindow;
