import React, { useCallback } from "react";
import InputBar from "../../atoms/InputBar";
import SelectBar from "../../atoms/SelectBar";
import { AlignLayout } from "../../styles/globalStyle";
import { useDispatch } from "react-redux";
import map from "lodash/map";
import {
  SET_SELECT_INPUT_BINDING_VALUE,
  DELETE_SELECT_INPUT_BINDING_VALUE,
} from "../../reducer/action/inputForm";
import DeleteButton from "../../atoms/DeleteButton";

const MapToComponent = (type) => {
  switch (type) {
    case "Select":
      return SelectBar;
    case "Input":
      return InputBar;
    default:
      return InputBar;
  }
};

function InputSelectBind(props) {
  const { elementIndex, values, patterns, align = "center" } = props;
  const dispatch = useDispatch();
  const handleChange = useCallback(
    (elementIndex, valueIndex) => {
      return (e) => {
        dispatch({
          type: SET_SELECT_INPUT_BINDING_VALUE,
          payload: {
            elementIndex: elementIndex,
            valueIndex: valueIndex,
            value: e.target.value,
          },
        });
      };
    },
    [dispatch]
  );
  const handleDelete = useCallback(
    (elementIndex) => {
      return (e) => {
        dispatch({
          type: DELETE_SELECT_INPUT_BINDING_VALUE,
          payload: {
            elementIndex: elementIndex,
          },
        });
      };
    },
    [dispatch]
  );

  return (
    <AlignLayout align={align}>
      {map(patterns, (pattern, index) => {
        const Component = MapToComponent(pattern.type);

        return (
          <Component
            key={index}
            value={values[index]}
            {...props}
            {...pattern}
            handleChange={handleChange(elementIndex, index)}
          />
        );
      })}
      <DeleteButton {...props} handleClick={handleDelete(elementIndex)} />
    </AlignLayout>
  );
}

export default InputSelectBind;
