import React from "react";
import AddIcon from "@material-ui/icons/AddCircle";
import { AddCircleIcon } from "./style";
import { AlignLayout } from "../../styles/globalStyle";

function AddButton(props) {
  const { handleClick, disabled } = props;
  return (
    <AlignLayout align={"center"}>
      <AddCircleIcon onClick={handleClick} disabled={disabled} {...props}>
        <AddIcon />
      </AddCircleIcon>
    </AlignLayout>
  );
}

export default AddButton;
