import styled from "styled-components";
import TitleBar from "../../atoms/TitleBar";

export const Option = styled.button`
  font-size: 20px;
  width: 100%;
  heigth: auto;
  margin: 10px;
  text-align: left;
  border-radius: 30px;
  padding: 10px;
  padding-left: 20px;
  border: ${(props) => (props.selected ? "3px solid blue" : "1px solid gray")};

  &:hover {
    cursor: pointer;
  }
`;

export const OptionLayout = styled.div`
  width: 50%;
`;

export const AlertBar = styled(TitleBar)`
  color: red;
`;
