import React, { useCallback } from "react";
// import TitleBar from "../../atoms/TitleBar";
// import InputSelectBind from "../../components/InputSelectBinding";
import map from "lodash/map";
import { useDispatch, useSelector } from "react-redux";
import mapper from "./mapper";
import { InputFormLayout } from "./style";

function InputForm(props) {
  const { customStep = null, getStructureFunc, disabled, style } = props;
  const { pages, step } = useSelector((state) => getStructureFunc(state));
  const page = pages[customStep != null ? customStep : step];
  const dispatch = useDispatch();
  const handleChange = useCallback(
    (action, index) => {
      return (e) => {
        dispatch({
          type: action,
          payload: { e: e, index: index },
        });
      };
    },
    [dispatch]
  );
  const handleClick = useCallback(
    (action, index) => {
      return (e) => {
        dispatch({
          type: action,
          payload: { e: e, index: index },
        });
      };
    },
    [dispatch]
  );

  const CustomComponent = page.customComponent;
  return (
    <InputFormLayout>
      {CustomComponent ? (
        <CustomComponent style={style} />
      ) : (
        map(page.elements, (element, index) => {
          const MapComponent = mapper[element.type];
          return (
            MapComponent && (
              <MapComponent
                key={index}
                elementIndex={index}
                {...element}
                disabled={disabled}
                style={style}
                handleClick={handleClick(element.actionHandleClick, index)}
                handleChange={handleChange(element.actionHandleChange, index)}
              />
            )
          );
        })
      )}
      {CustomComponent
        ? ""
        : map(page.footers, (footer, index) => {
            const MapComponent = mapper[footer.type];
            return (
              MapComponent && (
                <MapComponent
                  key={index}
                  elementIndex={index}
                  {...footer}
                  style={style}
                  disabled={disabled}
                  handleClick={handleClick(footer.actionHandleClick, index)}
                  handleChange={handleChange(footer.actionHandleChange, index)}
                />
              )
            );
          })}
    </InputFormLayout>
  );
}

export default InputForm;
