import React, { useCallback } from "react";
import { useDispatch } from "react-redux";
import NextButton from "../../atoms/NextButton";
import PreviousButton from "../../atoms/PreviousButton";
import {
  SET_NEXT_STEP,
  SET_PREVIOUS_STEP,
} from "../../reducer/action/inputForm";
import { AlignLayout, FixedLayout } from "../../styles/globalStyle";

function PageController(props) {
  const { left, right, top ,disabled} = props;

  const dispatch = useDispatch();
  const handlePreviousClick = useCallback(() => {
    dispatch({ type: SET_PREVIOUS_STEP });
  }, [dispatch]);
  const handleNextClick = useCallback(() => {
    dispatch({ type: SET_NEXT_STEP });
  }, [dispatch]);
  return (
    <AlignLayout>
      <FixedLayout left={left} top={top}>
        <PreviousButton handleClick={handlePreviousClick} disabled={disabled}/>
      </FixedLayout>
      <FixedLayout right={right} top={top}>
        <NextButton handleClick={handleNextClick} disabled={disabled}/>
      </FixedLayout>
    </AlignLayout>
  );
}

export default PageController;
