import styled from "styled-components";

export const AbsoluteBox = styled.div`
  position: absolute;
  background-color: darkblue;
  left: ${(props) => props.x}px;
  top: ${(props) => props.y - 30}px;
  display: ${(props) => (props.hide ? "none" : "block")};
  padding: 5px;
  font-size: 18px;
  color:white;
`;
