import {
  INIT_STRUCTURE,
  SET_SELECT_INPUT_BINDING_VALUE,
  ADD_INPUT_BINDING_VALUE,
  DELETE_SELECT_INPUT_BINDING_VALUE,
  SET_NEXT_STEP,
  SET_PREVIOUS_STEP,
  SET_STEP,
  SET_RATE,
} from "../../action/inputForm";
import initState from "./initInputForm";
import { inputSelectBindingTemplate } from "../../../Page/Structure";
import cloneDeep from "lodash/cloneDeep";
import toInteger from "lodash/toInteger";

const structure = (state = [], action) => {
  switch (action.type) {
    case INIT_STRUCTURE: {
      const nextState = cloneDeep(initState);

      return nextState;
    }
    case ADD_INPUT_BINDING_VALUE: {
      const nextState = { ...state };
      nextState.pages[state.step].elements.push(
        cloneDeep(inputSelectBindingTemplate)
      );

      return nextState;
    }
    case SET_SELECT_INPUT_BINDING_VALUE: {
      const nextState = { ...state };
      const { elementIndex, valueIndex, value } = action.payload;

      const thisElement = nextState.pages[state.step].elements[elementIndex];
      thisElement.values[valueIndex] = value;

      if (thisElement.middlewareFunc) {
        thisElement.middlewareFunc(thisElement);
      }

      return nextState;
    }
    case DELETE_SELECT_INPUT_BINDING_VALUE: {
      const nextState = { ...state };
      const { elementIndex } = action.payload;

      nextState.pages[state.step].elements.splice(elementIndex, 1);

      return nextState;
    }
    case SET_NEXT_STEP: {
      const nextState = { ...state };
      if (nextState.step < nextState.pages.length - 1) nextState.step++;

      return nextState;
    }
    case SET_PREVIOUS_STEP: {
      const nextState = { ...state };

      if (nextState.step > 0) nextState.step--;

      return nextState;
    }
    case SET_STEP: {
      const nextState = { ...state };
      nextState.step = toInteger(action.payload);

      return nextState;
    }
    case SET_RATE: {
      const nextState = { ...state };
      const step = nextState.step;

      const { index, e } = action.payload;
      nextState.pages[step].elements[index].value = e.target.value;

      return nextState;
    }
    default:
      return state;
  }
};

export default structure;
