import React from "react";
import LabelBar from "../LabelBar";
import { Input, Layout } from "./style";

function InputBar(props) {
  const { handleChange, title } = props;

  return (
    <Layout {...props}>
      <LabelBar content={title} />
      <Input onChange={handleChange} {...props} disabled={props.disabled} />
    </Layout>
  );
}

export default InputBar;
