import React, { useEffect, useRef } from "react";
import FloatingWindow from "./components/FloatingWindow";
import InputForm from "./Page/InputForm";
import Page from "./Page";
import { APILayout, BodyLayout, HeadLayout } from "./styles/globalStyle";
import { useDispatch, useSelector } from "react-redux";
import { INIT_STRUCTURE } from "./reducer/action/inputForm";
import PageController from "./components/PageController";

const getStructureFunc = (state) => state.structure;

const pageControlParams = {
  type: "page_controller",
  left: "20px",
  right: "20px",
  top: "50%",
};

function API() {
  const dispatch = useDispatch();
  const { step, pages } = useSelector((state) => state.structure);
  const ref = useRef();

  useEffect(() => {
    dispatch({ type: INIT_STRUCTURE });
  }, [dispatch]);

  useEffect(() => {
    if (!ref.current) return;
    const maxWidth = ref.current.scrollWidth;
    const leftNow = ref.current.scrollLeft;
    const vw = document.body.scrollWidth;
    const widhPerPage = maxWidth / pages.length;

    if (vw < (step + 1) * widhPerPage) {
      if (leftNow + widhPerPage * 3 > maxWidth) {
        ref.current.scrollLeft = maxWidth;
      } else ref.current.scrollLeft += widhPerPage * 3;
    } else if (leftNow > step * widhPerPage) {
      if (leftNow - widhPerPage * 3 < 0) {
        ref.current.scrollLeft = 0;
      } else ref.current.scrollLeft -= widhPerPage * 3;
    }
  }, [step, ref, pages]);
  return (
    step != null && (
      <APILayout>
        <HeadLayout>
          <FloatingWindow
            ref={ref}
            VisualizeComponent={InputForm}
            step={step}
            pages={pages}
            visualizeAttribute={{ getStructureFunc: getStructureFunc }}
          />
        </HeadLayout>
        <BodyLayout>
          <PageController {...pageControlParams} />
          <Page getStructureFunc={getStructureFunc} />
        </BodyLayout>
      </APILayout>
    )
  );
}

export default API;
