import toInteger from "lodash/toInteger";
import React, { useCallback } from "react";
import { useDispatch } from "react-redux";
import { SET_STEP } from "../../reducer/action/inputForm";
import { Order, WindowLayout } from "./style";

const style = {
  fontSize: "6px",
  padding: "1px",
  margin: "1px auto 1px auto",
  height: "16px",
  width: "70%",
};

function CombineWindow(props) {
  const dispatch = useDispatch();
  const { index, step, VisualizeComponent, visualizeAttribute } = props;
  const handleClick = useCallback(() => {
    dispatch({ type: SET_STEP, payload: index });
  }, [index, dispatch]);

  return (
    <WindowLayout onClick={handleClick} isThisPage={index === step}>
      <VisualizeComponent
        {...visualizeAttribute}
        customStep={index}
        disabled={true}
        style={style}
      />
      <Order>{toInteger(index) + 1}</Order>
    </WindowLayout>
  );
}

export default CombineWindow;
