import React from "react";
import RubbishBinIcon from "@material-ui/icons/Delete";
import { DeleteIcon } from "./style";

function DeleteButton(props) {
  const { handleClick, disabled } = props;
  
  return (
    <DeleteIcon
      onClick={handleClick}
      disabled={disabled}
      {...props}
    >
      <RubbishBinIcon />
    </DeleteIcon>
  );
}

export default DeleteButton;
